<template>
  <div class="w-100 position-relative">
    <div class="w-100 d-flex justify-content-between mb-3" v-if="connection">
      <b-button class="rounded-circle dailer-pad-number " type="button" title="transfer call" variant="secondary" 
        @click="show_types = !show_types">
        <div class="align-items-center d-flex flex-column justify-content-center text-center">
          <div>
            <i  class="fe fe-phone-forwarded"></i>
          </div>
          <p class="mb-0 text">Transfer</p>
       </div>
      </b-button> 
    <template v-if="show_types">
        <b-button variant="secondary" class="rounded-circle dailer-pad-number" v-b-tooltip:hover @click="type = 'cold';show_agents = true;" title="Cold">
          <div class="align-items-center d-flex flex-column justify-content-center text-center">
            <div>
              <i  class="fe fe-moon"></i>
            </div>
            <p class="mb-0 text">Cold</p>
          </div>
      </b-button>
      <b-button variant="secondary" class="rounded-circle dailer-pad-number" v-b-tooltip:hover @click="type = 'warm';show_agents = true;">
        <div class="align-items-center d-flex flex-column justify-content-center text-center">
          <div>
            <i  class="fe fe-sun"></i>
          </div>
          <p class="mb-0 text">Warm</p>
       </div>
      </b-button>
       
      
    </template>
  </div>
    <b-card class="card-inactive my-4" v-if="show_agents && show_types" body-class="pt-2">
      <div class="input-group phone-pad-section align-items-center mb-auto w-100">
        <input placeholder="Enter Phone Number" class="bg-transparent border-0 font-weight-normal form-control h2 mb-0 p-0 text-center text-white"
         v-model="phone_number" />
      </div>
      <div class="d-flex flex-nowrap align-items-center mb-3">
        <span class="border-secondary border-top w-50"></span>
        <span class="small px-3 text-white">OR</span>
        <span class="border-secondary border-top w-50"></span>
      </div>
      <b-dropdown block id="dropdown-1" text="Agents" variant="white"  size="sm" 
      toggle-class="rounded-top-0 d-flex justify-content-between align-items-center">
        <b-dropdown-item v-for="(item, index) in users" :key="index" @click="transferCall(item.phone_number)">{{ item.user_name }}</b-dropdown-item>
      </b-dropdown>
  </b-card>
  </div>
</template>

<script>
  import conference from "../../../js/dialer/conference";
  export default {
    props: {
      connection: Object,
    },
    data() {
      return {
        users: [],
        show_types: false,
        show_agents: false,
        type: "",
        phone_number: "",
      };
    },
    mounted: async function() {
      this.users = await conference.getAgents();
    },
    methods: {
      transferCall(number) {
        let response = conference.transferCall(this.connection.parameters.CallSid, this.type, number);
        console.log(response);
      },
    },
    watch: {
      phone_number: conference.debounce(function(number) {
        let response = conference.transferCall(this.connection.parameters.CallSid, this.type, number);
        console.log(response);
      }, 500),
    },
  };
</script>
